.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: black;
  min-height: 120vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: white;
  overflow: scroll;
}

.img {
  height: 200px;
  width: 200px;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

$pokeRed: #e8332a;
$pokeGrey: #bfbfc9;
$pokeBlue: #3661ac;
$pokeYellow: #ffcb08;

body {
  font-family: sans-serif;
  background-color: $pokeBlue;
  font-family: "Iceland", cursive;
}
.searchBar {
  padding: 20px 0;
  text-align: center;
  background-color: $pokeRed;
  border-radius: 8px 8px 0 0;
}
label {
  font-size: 1.3rem;
}
input[type="text"] {
  height: 40px;
  padding-left: 10px;
  font-family: "Iceland", cursive;
}
#pokeOutput {
  padding: 20px 50px 30px;
}
#pokeImage {
  display: block;
  margin: 20px 0;
  width: 180px;
  position: relative;
  left: -45px;
}
.base-title {
  font-size: 1.3rem;
  font-weight: 700;
}
.stat {
  font-weight: 700;
}
.poke-name-num {
  display: inline-block;
  float: left;
  margin-left: 5px;
}
.size {
  display: inline-block;
  float: right;
  margin-right: 5px;
}
.name {
  display: block;
  text-transform: capitalize;
  font-size: 1.6rem;
}
.poke-info {
  display: inline-block;
  margin-right: 2px;
}

.container {
  width: 450px;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  background-color: #fff;
  color: #000;
  border-radius: 15px;
  box-shadow: 10px 10px 0px 0px rgba(0, 0, 0, 0.75);
}
.bottom-container {
  background-color: red;
  font-weight: bold;

}
tr{
  border: solid;
  border-width: 1px 0;
}
td {
  border: solid;
  border-width: 1px 1;
}
th {
  border: solid;
  border-width: 1px 1;
}
.image-container {
  height: 300px;
  background-color: $pokeGrey;
  white-space: nowrap;
  border: 1px solid $pokeRed;
  color: #fff;
  font-size: 1.2rem;
}
button {
  display: inline;
  margin: 0;
  background-color: #fff;
  color: $pokeRed;
  border: 1px solid $pokeRed;
  position: relative;
  left: 0px;
  outline: none;
  cursor: pointer;
}
button:hover {
  border: 2px solid $pokeRed;
}
button:active {
  background-color: #f4f1f1;
}
.bigBtn {
  height: 40px;
  width: 120px;
  font-weight: 700;
  position: relative;
  top: -2px;
}
.changeBtn {
  height: 40px;
  width: 54px;
  font-size: 1.5rem;
}
table {
  border-collapse: collapse;
  width: 100%;
  font-size: 1.2rem;
}

td,
th {
  border: 1px solid $pokeRed;
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: $pokeRed;
}
.pokeType {
  padding: 5px;
  color: #fff;
  border-radius: 5px;
  text-transform: capitalize;
  margin-left: 5px;
  font-weight: 700;
}
